import React from "react";
import styled from "styled-components";
import { Link } from "react-scroll";

// Assets
import LogoImg from "../../assets/svg/Logo"; // Asegúrate que la ruta a tu logo sea correcta
import LogoUnap from "../../assets/img/logo_unap_solo.png";
// Icons
import { FaMapMarkerAlt, FaPhoneAlt, FaEnvelope } from 'react-icons/fa';
import { FaFacebook, FaTwitter, FaYoutube } from 'react-icons/fa';

export default function Footer() {
  const getCurrentYear = () => {
    return new Date().getFullYear();
  };

  return (
    <Wrapper>
      <div className="darkBg">
        <div className="container">
          {/* SECCIÓN PRINCIPAL DEL FOOTER CON COLUMNAS */}
          <InnerWrapper>
            {/* Columna 1: Logo e Identidad */}
            <Column>
              <Link className="pointer flexNullCenter" to="home" smooth={true}>
                <img 
                  src={LogoUnap} 
                  alt="Logo UNAP" 
                  style={{  height: "50px" }} // ajusta el tamaño según necesites
                /> {/* <h1 style={{ marginLeft: "15px" }} className="font20 extraBold">
                  UNAP
                </h1>
                */}
              </Link>   
              <p className="whiteColor font13" style={{ padding: "30px 0" }}>
                Universidad Nacional del Altiplano. Comprometidos con la excelencia académica y el desarrollo regional.
              </p>
            </Column>

            {/* Columna 2: Contacto */}
            <Column>
              <Title className="font15 extraBold whiteColor">Contacto</Title>
              <ContactItem>
                <FaMapMarkerAlt color="#7620ff" />
                <p className="whiteColor font13" style={{ marginLeft: "10px" }}>
                  Sede Central: Av. Floral Nº 1153, Puno - Perú
                </p>
              </ContactItem>
              <ContactItem>
                <FaPhoneAlt color="#7620ff" />
                <p className="whiteColor font13" style={{ marginLeft: "10px" }}>
                  Central Telefónica: (051) 363682
                </p>
              </ContactItem>
              {/* <ContactItem>
                <FaEnvelope color="#7620ff" />
                <p className="whiteColor font13" style={{ marginLeft: "10px" }}>
                  OTI: tecnologias@unap.edu.pe
                </p>
              </ContactItem> */}
            </Column>

            {/* Columna 3: Enlaces de Interés */}
            <Column>
              <Title className="font15 extraBold whiteColor">Enlaces de Interés</Title>
              <FooterLink href="https://admision.unap.edu.pe/" target="_blank" rel="noopener noreferrer">Admisión</FooterLink>
              <FooterLink href="https://portal.unap.edu.pe/" target="_blank" rel="noopener noreferrer">Pagina Principal UNA-Puno</FooterLink>
              <FooterLink href="https://intranet.unap.edu.pe/" target="_blank" rel="noopener noreferrer">Intranet</FooterLink>
              <FooterLink href="https://transparencia.unap.edu.pe/" target="_blank" rel="noopener noreferrer" >Transparencia</FooterLink>
            </Column>

            {/* Columna 4: Redes Sociales */}
            <Column>
               <Title className="font15 extraBold whiteColor">Síguenos</Title>
               <SocialLinks>
                <a href="https://www.facebook.com/unapunooficial" target="_blank" rel="noopener noreferrer"><SocialIcon><FaFacebook /></SocialIcon></a>
                {/* <a href="https://twitter.com/unapuno" target="_blank" rel="noopener noreferrer"><SocialIcon><FaTwitter /></SocialIcon></a>
                <a href="https://www.youtube.com/unapuno" target="_blank" rel="noopener noreferrer"><SocialIcon><FaYoutube /></SocialIcon></a> */}
               </SocialLinks>
            </Column>

          </InnerWrapper>
          
          {/* BARRA INFERIOR DE COPYRIGHT */}
          <BottomBar>
            <StyleP className="whiteColor font13">
              © {getCurrentYear()} - <span className="purpleColor font13">Universidad Nacional del Altiplano - Oficina de Tecnologias de la Información</span>. Todos los derechos reservados.
            </StyleP>
            <Link className="whiteColor animate pointer font13" to="home" smooth={true} offset={-80}>
              Volver Arriba
            </Link>
          </BottomBar>

        </div>
      </div>
    </Wrapper>
  );
}

// --- STYLED COMPONENTS ---

const Wrapper = styled.div`
  width: 100%;
`;

const InnerWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 60px 0 40px 0;
  gap: 30px; /* Espacio entre columnas */

  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1; /* Permite que las columnas crezcan y ocupen el espacio */
  min-width: 180px; /* Ancho mínimo para evitar que se aplasten */
`;

const Title = styled.h3`
  margin-bottom: 20px;
`;

const ContactItem = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 10px;
`;

const FooterLink = styled.a`
  color: #fff;
  font-size: 13px;
  margin-bottom: 10px;
  text-decoration: none;
  transition: color 0.3s;
  
  &:hover {
    color: #7620ff; /* Color púrpura al pasar el mouse */
  }
`;

const SocialLinks = styled.div`
  display: flex;
  gap: 15px; /* Espacio entre iconos */
`;

const SocialIcon = styled.div`
  color: #fff;
  font-size: 20px;
  transition: color 0.3s;

  &:hover {
    color: #7620ff;
  }
`;

const BottomBar = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-top: 1px solid #444; /* Línea divisoria */

  @media (max-width: 550px) {
    flex-direction: column;
  }
`;

const StyleP = styled.p`
  @media (max-width: 550px) {
    margin-bottom: 20px;
  }
`;